@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&family=Commissioner:wght@100&family=EB+Garamond&family=Noto+Sans+Mono&family=Righteous&display=swap);
/* custom property */
:root{
  --clr-primary: hsl(0, 0%, 97%);
}

*,
*::before,
*::after{
  box-sizing: border-box;
}


body {
  background-image: url(/random-quote-generator/static/media/background3.2e04a38f.jpeg);
  background-size: cover;
  display: grid;
  place-items: center;
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#quote-box{
  background: rgba(255,255,255,0.5);
  border-radius: 20px;
  background-size: cover;
  max-width: 100ch;
  padding: 2rem;
  box-shadow:
  4.5px 4.5px 3.6px rgba(0, 0, 0, 0.059),
  12.5px 12.5px 10px rgba(0, 0, 0, 0.085),
  30.1px 30.1px 24.1px rgba(0, 0, 0, 0.111),
  100px 100px 80px rgba(0, 0, 0, 0.17);

 display: grid;
 grid-template-rows: auto;
}
.btn{
 display: flex;
 justify-content: space-between; 
}

#new-quote{
    padding: 1em;
    font-size: 10px;
    font-family: 'Amatic SC', cursive;
    font-family: 'Commissioner', sans-serif;
    font-family: 'EB Garamond', serif;
    font-family: 'Noto Sans Mono', monospace;
    font-family: 'Righteous', cursive;
    background: rgba(255,255,255,0.5);
    color: black;
    border-radius: 10px;
    cursor: pointer;
    text-transform: uppercase;
    border: none;
}

#new-quote:hover{
    background-color: rgb(62, 120, 177);
    color: rgb(255, 255, 255);
}

.btn2{
    background-image: url(/random-quote-generator/static/media/twitter.9e2fa5c2.jpg);
    width:50px;
    /* background-color:rgb(122, 179, 235); */
    background-size: cover;
    border-radius: 20px;
    cursor: pointer;
    display: grid;
    place-items: center;
    min-height: 5vh;
    border: none;
}
